  created() {},
  mounted() {
    if (this.$route.params.id == 1) {
      this.subjectName = " ";
    } else if (this.$route.params.id == 2) {
      this.subjectName = "";
    } else if (this.$route.params.id == 3) {
      this.subjectName = "";
    } else {
      this.subjectName = " ";
    }
    //  
    if (this.$route.params.id == 1) {
      this.testsDetail.forEach((item) => {
        this.dataResult.push({
          question: item,
          answer: null,
        });
      });
    }
    //  

    //  
    if (this.$route.params.id == 2) {
      this.testsDetailHistory.forEach((item) => {
        this.dataResult.push({
          question: item,
          answer: null,
        });
      });
    }
    //  

    //  
    if (this.$route.params.id == 3) {
      this.testsDetailGeografiya.forEach((item) => {
        this.dataResult.push({
          question: item,
          answer: null,
        });
      });
    }
    //  
  },
  methods: {
    //  
    resultEnglish() {
      let sendData = [];

      this.dataResult.forEach((item, index) => {
        let answer = item.answer;

        let ritem = {
          title: item.question.title,
          isTrue: true,
          answer: answer,
          trueAnswer: null,
        };

        let trueAnswer;
        trueAnswer = this.testsDetail[index].answers.find(
          (item) => item.isTrue === true
        );

        if (!(answer == null)) {
          // for type test one select
          if (!answer.isTrue) {
            ritem.isTrue = false;
          }
        } else {
          ritem.isTrue = false;
          ritem.answer = null;
        }

        ritem.trueAnswer = trueAnswer;

        sendData.push(ritem);
      });

      this.viewResult = sendData;

      this.viewResult.forEach((item) => {
        if (item.isTrue) {
          this.count++;
        }
      });
    },
    //  
    //  
    resultHistory() {
      let sendData = [];

      this.dataResult.forEach((item, index) => {
        let answer = item.answer;

        let ritem = {
          title: item.question.title,
          isTrue: true,
          answer: answer,
          trueAnswer: null,
        };

        let trueAnswer;
        trueAnswer = this.testsDetailHistory[index].answers.find(
          (item) => item.isTrue === true
        );

        if (!(answer == null)) {
          // for type test one select
          if (!answer.isTrue) {
            ritem.isTrue = false;
          }
        } else {
          ritem.isTrue = false;
          ritem.answer = null;
        }

        ritem.trueAnswer = trueAnswer;

        sendData.push(ritem);
      });

      this.viewResult = sendData;

      this.viewResult.forEach((item) => {
        if (item.isTrue) {
          this.count++;
        }
      });
    },

    resultGeografiya() {
      let sendData = [];

      this.dataResult.forEach((item, index) => {
        let answer = item.answer;

        let ritem = {
          title: item.question.title,
          isTrue: true,
          answer: answer,
          trueAnswer: null,
        };

        let trueAnswer;
        trueAnswer = this.testsDetailGeografiya[index].answers.find(
          (item) => item.isTrue === true
        );

        if (!(answer == null)) {
          // for type test one select
          if (!answer.isTrue) {
            ritem.isTrue = false;
          }
        } else {
          ritem.isTrue = false;
          ritem.answer = null;
        }

        ritem.trueAnswer = trueAnswer;

        sendData.push(ritem);
      });

      this.viewResult = sendData;

      this.viewResult.forEach((item) => {
        if (item.isTrue) {
          this.count++;
        }
      });
    },
    //  
    clickRadio(index, answer) {
      this.dataResult[index].answer = answer;
    },
    againTest() {
      this.viewResult = false;
    },
  },
